import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap/dist/js/bootstrap.min.js"
import React, { useState } from 'react';
import './App.css';
import Inicio from './Componentes/Inicio';
import Lapaz from './Componentes/Lapaz';
import Oruro from './Componentes/Oruro';
import Cochabamba from './Componentes/Cochabamba';
import Santacruz from './Componentes/Santacruz';
import Beni from './Componentes/Beni';
import Pando from './Componentes/Pando';
import Potosi from './Componentes/Potosi'; 
import Chuquisaca from './Componentes/Chuquisaca';
import Tarija from './Componentes/Tarija';
import Formuycreditos from './Componentes/Formuycreditos'; // Importa la nueva página

function App() {
  const [currentPage, setCurrentPage] = useState('Inicio');

  const navigateTo = (page) => {
    setCurrentPage(page);
  };

  const renderPage = () => {
    switch (currentPage) {
      case 'Inicio':
        return <Inicio />;
      case 'Lapaz':
        return <Lapaz />;
      case 'Oruro':
        return <Oruro />;
      case 'Cochabamba':
        return <Cochabamba />;
      case 'Santacruz':
        return <Santacruz />;
      case 'Beni':
        return <Beni />;
      case 'Pando':
        return <Pando />;
      case 'Potosi':
        return  <Potosi />;
      case 'Chuquisaca':
        return <Chuquisaca />;
      case 'Tarija':
        return <Tarija />;
      case 'Formuycreditos': 
        return <Formuycreditos />;
      default:
        return <Inicio />;
    }
  };

  return (
    <div className="App">
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
          <ul className="navbar-nav">
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Inicio' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Inicio')}>
                Inicio
              </a>
            </li>
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Lapaz' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Lapaz')}>
                Lapaz
              </a>
            </li>
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Oruro' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Oruro')}>
                Oruro
              </a>
            </li>
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Cochabamba' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Cochabamba')}>
                Cochabamba
              </a>
            </li>
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Santacruz' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Santacruz')}>
                Santacruz
              </a>
            </li>
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Beni' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Beni')}>
                Beni
              </a>
            </li>
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Pando' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Pando')}>
                Pando
              </a>
            </li>
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Potosi' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Potosi')}>
                Potosi
              </a>
              </li>
              <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Chuquisaca' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Chuquisaca')}>
               Chuquisaca
              </a>
            </li>
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Tarija' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Tarija')}>
                Tarija
              </a>
            </li>
            
            <li className="nav-item">
              <a className={`nav-link ${currentPage === 'Formuycreditos' ? 'active' : ''}`} href="#" onClick={() => navigateTo('Formuycreditos')}>
                Formuycreditos
              </a>
            </li>
          </ul>
        </nav>

      <div className="content mt-4">{renderPage()}</div>

      <footer className="bg-light p-3 text-center">
        <p>&copy; Copyright© 2023 LaPaz - Bolivia. Todos los derechos reservados.</p>
      </footer>
    </div>
  );
}
export default App;