
import { Carousel } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";

import mr1 from '../img/mr1.jpg';
import mr2 from '../img/mr2.jpg';
import mr3 from '../img/mr3.jpg';
import mr4 from '../img/mr4.jpg';
import mr5 from '../img/mr5.jpg';
import React from 'react';
import App from "../App";



const Lapaz = () => {
  return (
    <div>
      <section className="jumbotron">
          <h1 className="display-4">DANZAS EN LA PAZ</h1>
      </section>
        <div className="col-md-6 acordeon-container">
            <h3 className="sub">MORENADA</h3>
             <p>La Morenada es una de las danzas más representativas de la cultura occidental de Bolivia. Esta importancia sobresale por la difusión del baile y la música en las fiestas patronales y cívicas de las ciudades de La Paz, El Alto, Oruro, Sucre, Potosí, Cochabamba y sus alrededores.</p>
             <p>La danza Morenada (danza de los Morenos) es un baile boliviano que tiene su raíz en la minería, es una danza 100% boliviana.</p>
             <p> La danza Morenada representa el traslado de negros esclavos hasta las minas de Potosí, en tiempos de mita y la encomienda. Mancilla continua afirmando que para mantener su brutal hegemonía en America y su esplendor imperial en Europa, los conquistadores echaron mano de todo lo que pudieron y los negros que eran considerados mas esclavos que los indígenas eran mas despreciados inclusive por el mismo indio.</p>   
              <h3 className="sub">HISTORIA DE LA MORENADA</h3>
             <p>En la danza Morenada hay que aclarar que no todos los personajes de la Morenada bailan con una careta de negro, esta queda reservada para los Caporales (capataces negros) y los Morenos. La máscara de Moreno más antigua que se conserva data del año 1875 y fue realizada por Pánfilo Flores. Esa máscara tiene un aspecto mucho más sutil y delicado que los ejemplares usados hoy en día, en los que se destacan los ojos extremadamente desorbitados y la lengua saliente, elementos que supuestamente representan el cansancio y el efecto producido por el soroche (enfermedad de la altura). El sonido de la matraca de los morenos se asocia generalmente con el chirrido producido por el roce de las cadenas que portaban los esclavos.</p>           
        </div>
                <div className="car">
                    <Carousel>
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src={mr1}
                    alt="Primera imagen"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src={mr2}
                    alt="Segunda imagen"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src={mr3}
                    alt="Tercera imagen"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src={mr4}
                    alt="Cuarta imagen"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src={mr5}
                    alt="quinta imagen"
                    />
                </Carousel.Item>
                </Carousel>
              </div>
            </div>
             
      
      
  
   
  );
};

export default Lapaz;