// Importa el componente Carousel de Bootstrap
import { Carousel } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import imgmen from '../img/imgmen.jpg';
import imgmen1 from '../img/imgmen1.jpg';
import imgmen2 from '../img/imgmen2.jpg';
import imgmen3 from '../img/imgmen3.jpg';
import imgmen4 from '../img/imgmen4.jpg';
import imgmen5 from '../img/imgmen5.jpg';
import imgmen6 from '../img/imgmen6.jpg';
import imgmen7 from '../img/imgmen7.jpg';
import imgmen8 from '../img/imgmen8.jpg';
import React from 'react';
import App from "../App";
const Inicio = () => {
  return (
    <div>
      <section className="jumbotron text-center">
        <div className="container">
          <h1 className="display-4">DANZAS DE BOLIVIA</h1>
        </div>
      </section>

      <div className="container mt-4">
            <div className="row">
                <div className="col-md-6">
                    <p>
                    Las danzas de Bolivia se caracterizan por las regiones en que se bailan, estas pueden ser rituales de agradecimiento a la tierra, una forma de expresión de la identidad cultural de los pueblos o una reivindicación.
                    Las danzas originarias del país boliviano se distinguen a primera vista ya que son de ritmos alegres y vestimentas coloridos, a esto podemos añadir que emplean plumas y cueros de animales en la vestimenta.
                    Podemos decir que el folklore de Bolivia es el resultado del sincretismo cultural de las tradiciones, costumbres y creencias de indígenas-originarios y españoles durante el periodo de la conquista.
                    </p>
                    
                </div>
                    
                </div>
            </div>

        <div className="container">
            <Carousel>
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={imgmen}
                alt="Primera imagen"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={imgmen1}
                alt="Segunda imagen"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={imgmen2}
                alt="Tercera imagen"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={imgmen3}
                alt="Cuarta imagen"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={imgmen4}
                alt="quinta imagen"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={imgmen5}
                alt="Sexta imagen"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={imgmen6}
                alt="Septima imagen"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={imgmen7}
                alt="Octava imagen"
                />
            </Carousel.Item>
            <Carousel.Item>
                <img
                className="d-block w-100"
                src={imgmen8}
                alt="Novena imagen"
                />
            </Carousel.Item>
            </Carousel>
        </div>

      <div className="container mt-4">
        <h2>HISTORIA</h2>
              <p>
              Las danzas bolivianas están influenciadas en las costumbres y creencias prehispánicas, indígenas, mestizas, tradiciones coloniales europeas y africanas evidenciando un importante resultado de mestizaje y sincretismo cultural

              </p>
              <p>
              pertenecen al patrimonio cultural del país. Bolivia al poseer variadas zonas geográficas cuenta con una población diversa y rica en cultura y tradiciones, esto se refleja en la inmensa cantidad de danzas existentes en todo el país. Las danzas bolivianas están influenciadas en las costumbres y creencias prehispánicas, indígenas, mestizas, tradiciones coloniales europeas y africanas evidenciando un importante resultado de mestizaje y sincretismo cultural.
              </p>
              <p>
              La danza es un elemento muy importante dentro de la cultura boliviana existiendo festividades masivas y colectivas denominada entradas donde conjuntos y fraternidades ocupan espacios públicos para danzar, este fenómeno social solo ocurre en esa magnitud en Bolivia existiendo más de 1200 festividades dentro del país que se repiten anualmente donde se interpretan diversas danzas bolivianas,
              </p>
        {/* Aquí puedes agregar información sobre logros académicos y deportivos */}
      </div>

    </div>
  );
};

export default Inicio;